.grid {
    display: grid;
    grid-template-areas: " body ";
    grid-template-columns:  99%;
    grid-template-rows: 80%;
    background: rgba(255, 255, 255, 0);


}

.item2 {
    grid-area: field;
    z-index: 7777;
    background-color: white;
}

.item3 {
    grid-area: field;
}

.item {
    display: grid;
    grid-area: body;
    height: 100%;
    max-height: 100vh;
    grid-template-columns: repeat(20, 1fr);
    grid-template-rows: repeat(20, 1fr);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); /* Параметры тени */
    border: 1px solid #575656;
    /*padding: 10px;*/


}

@media screen and (min-width: 916px) {
    .grid {
        display: grid;
        grid-template-areas: "field body field2";
        grid-template-columns: 25% max-content 25%;
        background: rgba(255, 255, 255, 0);
        width: 100%;
        height: 100%;
    }
    .item {
        margin-top: 5%;
        max-height: 550px;
        display: grid;
        grid-template-columns: repeat(20, 1fr) 300px;
        grid-template-rows: repeat(20, 1fr);

        border: 1px solid #575656;
        /*padding: 10px;*/
        grid-area: body;

    }
}

div button {
    background: rgb(255, 255, 255);
    border: 1px solid #f5f5f5; /* Параметры рамки */
