.curtain {
    background: black;
    grid-row: 1/span 8;
    grid-column: 1/span 20;
    transition: 500ms;
    opacity: 0%;
    z-index: -1;
}

.curtainActive {
    background: black;
    grid-row: 1/span 8;
    opacity: 20%;
    grid-column: 1/span 20;
    z-index: 4;

}

.curtainAll {
    grid-row: 1/span 20;
    grid-column: 1/span 20;
    z-index: -1;
}

.curtainAllActive {
    grid-row: 1/span 20;
    grid-column: 1/span 20;
    z-index: 1;
}

